const Home = () => {
    const posts = [
      {
        image: "https://via.placeholder.com/300x200.png?text=Image+1",
        title: "Loudest à la Madison #1 (L'intégral)",
        description: "We focus on ergonomics and meeting you where you work. It's only a keystroke away.",
        date: "22 April 2021",
        comments: 10,
      },
      {
        image: "https://via.placeholder.com/300x200.png?text=Image+2",
        title: "Loudest à la Madison #2 (L'intégral)",
        description: "We focus on ergonomics and meeting you where you work. It's only a keystroke away.",
        date: "22 April 2021",
        comments: 10,
      },
      {
        image: "https://via.placeholder.com/300x200.png?text=Image+3",
        title: "Loudest à la Madison #3 (L'intégral)",
        description: "We focus on ergonomics and meeting you where you work. It's only a keystroke away.",
        date: "22 April 2021",
        comments: 10,
      },
    ];
  
    return (
      <div className="bg-gray-50 min-h-screen">
        {/* Header */}
        <header className="text-center py-12">
          <p className="text-sky-400 font-semibold">Practice Advice</p>
          <h1 className="text-xl text-gray-800 md:text-5xl font-bold my-4">Featured Posts</h1>
          <p className="text-gray-500 text-sm max-w-2xl mx-auto font-medium">
            Problems trying to resolve the conflict between <br/> the two major realms
            of Classical physics: Newtonian mechanics.
          </p>
        </header>
  
        {/* Posts Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 px-6 md:px-12">
          {posts.map((post, index) => (
            <div key={index} className="bg-white rounded-lg shadow-md overflow-hidden">
              <div className="relative">
                <img
                  src={post.image}
                  alt={post.title}
                  className="w-full h-48 object-cover"
                />
                <span className="absolute top-2 left-2 bg-orange-500 text-white text-xs px-2 py-1 rounded">
                  NEW
                </span>
              </div>
              <div className="p-4">
                <p className="text-blue-500 text-sm">Google Trending Now</p>
                <h3 className="text-lg font-semibold mt-2">{post.title}</h3>
                <p className="text-gray-600 text-sm mt-1">{post.description}</p>
                <div className="text-gray-400 text-xs mt-3">
                  <span>{post.date}</span> · <span>{post.comments} comments</span>
                </div>
                <a
                  href="#"
                  className="text-blue-500 text-sm font-medium mt-4 inline-block"
                >
                  Learn More &gt;
                </a>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  };
  
  export default Home;
  